import { ReactComponent as Switch } from "./images/switch.svg";
import { ReactComponent as CircleE } from "./images/circleE.svg";
import { ReactComponent as Circle } from "./images/circle.svg";
import { LangContext } from "../../../context/LangContext";
import { useContext, useState, useLayoutEffect, useRef } from "react";
import './styles/section1.css';
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {gsap, Power3} from 'gsap'
gsap.registerPlugin(ScrollTrigger)

export default function Section1() {
    gsap.registerPlugin(ScrollTrigger)

    const [slider, setSlider] = useState(1)
    const {lang} = useContext(LangContext);

    const h1 = useRef(null);
    const h3 = useRef(null);
    const line = useRef(null);
    const slide1 = useRef(null);
    const slide2 = useRef(null);
    const slide3 = useRef(null);
    const descript = useRef(null);
    const descript2 = useRef(null);
    const section1 = useRef(null);

    useLayoutEffect(() => {
        gsap.to(section1.current, {
            scrollTrigger: {
                scrub: 1,
                trigger: ".section1",
                start: 'bottom bottom',
            },
            opacity: 0,
            scale: 0.9
        })
       gsap.from([h3.current, h1.current, descript.current, descript2.current], {
            scrollTrigger: {
                trigger: ".section1",
                start: 'center bottom',
            },
            ease: Power3.easeOut(),
            duration: 1,
            
            stagger: {
                amount: 2
            },
            y: 100,
            opacity: 0
            
        })
        gsap.from( [ slide1.current, slide2.current, slide3.current], {
            scrollTrigger: {
                trigger: ".section1",
                start: 'center bottom'
            },
            ease: Power3.easeOut(),
            duration: 1, 
            stagger: .5,
            x: 100,
            opacity: 0
            
        })
        gsap.from(line.current, {
            scrollTrigger: {
                trigger: '.horizontal-line',
            },
            scrub: 1,
            x: -100,
            opacity: 0,
            duration: 1
        })
    }, [])

    function swipeFn() {
            if (slider <= 2) {
                setSlider(slider+1)
            } else {
                setSlider(prev => prev = 1)
            }
            console.log(slider)
        }
       
    

    return(
        <article id="section1" ref={section1} className="section1">
            <div className="gradient"></div>
            <div className="content">
            <section className="left">
                {!lang && <h3 ref={h3}>What is sos?</h3>}
                {lang && <h3 ref={h3} className="ru-h3">  SOS?</h3>}
                {!lang && <h1 ref={h1} className="ru-h1">sosial battle royale</h1>}
                {lang && <h1 ref={h3} className="ru-h1">  </h1>}
                <div ref={line} className="horizontal-line"></div>

                
                <div className="text-descript">
                    {!lang && <p ref={descript}>
                        Each round, <strong>you</strong> and   <strong> 15 </strong>other contestants compete to
                        escape a deadly island filled with monsters. The trick is: <strong> three </strong> people can survive. Will you run
                        solo or form friendships with others to escape? 
                        
                    </p>}
                    {lang && <p ref={descript}>
                           <strong>  </strong>   <strong> 15 </strong>  ,    
                         ,  .   ,   <strong>  </strong>  . 
                                 ,  ?
                        
                    </p>}
                </div>
                <div className="text-descript">
                    {!lang && <p ref={descript2}>
                    Making the right decisions could be the difference between <strong>life and death.  </strong>
                    </p>}
                    {lang && <p ref={descript2}>
                               <strong>   . </strong>
                    </p>}
                </div>
            </section>


            <section className="right">
                <div className="slider">
                    {slider === 1 && <div className="slides-container">
                    <div className="slides">
                        <img ref={slide1} className="slide1" src={require('./images/slide1.png')} alt="slide" />
                        <img ref={slide2} src={require('./images/slide2.png')} alt="slide" />
                        <img ref={slide3} src={require('./images/slide3.png')} alt="slide" />
                    </div>
                    <div className="step">
                        <Circle />
                        <CircleE />
                        <CircleE />
                    </div>
                    </div>}

                    {slider === 2 && <div className="slides-container">
                    <div className="slides">
                        <img className="slide1" src={require('./images/slide2.png')} alt="slide" />
                        <img src={require('./images/slide3.png')} alt="slide" />
                        <img src={require('./images/slide1.png')} alt="slide" />
                    </div>
                    <div className="step">
                        <CircleE />
                        <Circle />
                        <CircleE />
                    </div>
                    </div>}

                    {slider === 3 && <div className="slides-container">
                    <div className="slides">
                        <img className="slide1" src={require('./images/slide3.png')} alt="slide" />
                        <img src={require('./images/slide2.png')} alt="slide" />
                        <img src={require('./images/slide1.png')} alt="slide" />
                    </div>
                    <div className="step">
                        <CircleE />
                        <CircleE />
                        <Circle />
                    </div>
                    </div>}




                    <div className="swipe" onClick={swipeFn}>
                        <Switch />
                    </div>
                </div>
            </section>
            </div>
        </article>
    )
}