import './styles/main.css'
import { ReactComponent as Scroll } from './images/scroll.svg'
import { LangContext } from '../../../context/LangContext';
import { useContext, useLayoutEffect, useRef } from 'react';
import { gsap, Power3 } from 'gsap';

export default function Main() {
    const btn = useRef(null)
    const h1 = useRef(null)
    const h3 = useRef(null)
    const main = useRef(null)
    const story = useRef(null)
    const tlBtn = gsap.timeline({repeat: Infinity, repeatDelay: 2})

    let ease = Power3.easeOut()
    useLayoutEffect(() => {
        tlBtn.add(gsap.from(btn.current,  {
            scale: 1.5,
        }))
        gsap.to(main.current, {
            scrollTrigger: {
                scrub: 1,
                trigger: ".main",
                start: 'bottom bottom',
            },
            opacity: 0,
            scale: 0.8
        })
        gsap.from([h1.current, h3.current], {
            opacity: 0,
            y: 100,
            stagger: {
                amount: 1
            },
            ease: ease
        })
    }, [])
    const {lang, toRu, toEn} = useContext(LangContext);
    console.log(story)
    return(
        <main ref={main} className='main' >
            {!lang && <div className="main-center">
                <h1 ref={h1} >SURVIVE AT ALL COSTS</h1>
                <h3 ref={h3} >Experience new social battle royale game</h3>

                <button ref={btn} className="buy">
                    <p>Buy Now on Steam</p>
                    <div className="line"></div>
                    <p>$14,99</p>
                </button>
            </div>}
            {lang && <div className="main-center">
                <h1 ref={h1}  className='ru-h1'>  </h1>
                <h3 ref={h3}  className='ru-h3'>       </h3>

                <button ref={btn} className="buy">
                    <p>  Steam</p>
                    <div className="line"></div>
                    <p>$14,99</p>
                </button>
            </div>}
            {!lang && <a ref={story} href='#' className="main-bottom">
                <p>THE STORY</p>
                <Scroll />
            </a>}
            {lang && <a ref={story} href='#' className="main-bottom">
                <p></p>
                <Scroll />
            </a>}
        </main>
    )
}