import './styles/section2.css'
import { ReactComponent as CircleE } from "./images/circleE.svg";
import { ReactComponent as Circle } from "./images/circle.svg";
import { useContext, useState, useLayoutEffect, useRef } from "react";
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {gsap, Power3} from 'gsap'
import { LangContext } from '../../../context/LangContext';
gsap.registerPlugin(ScrollTrigger)

export default function Section2() {
    const {lang} = useContext(LangContext)
    const [hidden1, setHidden1] = useState(false)
    const [hidden2, setHidden2] = useState(false)
    const [hidden3, setHidden3] = useState(false)
    const h3 = useRef(null);
    const h1 = useRef(null);
    const section2 = useRef(null);
    const list1 = useRef(null);
    const list2 = useRef(null);
    const list3 = useRef(null);

    useLayoutEffect(() => {
        gsap.from([h3.current, h1.current], {
            scrollTrigger: {
                start: 'center bottom',
                trigger: '.section2'
            },
            duration: .5,
            opacity: 0,
            stagger: 0.5,
            y: 20,
            ease: Power3.easeOut()
            
        })
        gsap.from([list1.current, list3.current], {
            scrollTrigger: {
                start: 'center bottom',
                trigger: '.section2'
            },
            delay: 1,
            duration: .5,
            opacity: 0,
            stagger: 1,
            x: 20,
            ease: Power3.easeOut()
            
        })
        gsap.from([list2.current], {
            scrollTrigger: {
                start: 'center bottom',
                trigger: '.section2'
            },
            delay: 1.5,
            duration: .5,
            opacity: 0,
            stagger: 0.5,
            x: -20,
            ease: Power3.easeOut()
            
        })
        gsap.to(section2.current, {
            scrollTrigger: {
                scrub: 1,
                trigger: ".section2",
                start: 'bottom bottom',
            },
            opacity: 0,
            scale: 0.9
        })
    }, [])

    return(
        <article id='section2' ref={section2} className='section2'>
            <div className="gradient"></div>
            <div className="content" id='content'>
                {!lang && 
                <>
                    <h3 ref={h3}>Whats so special?</h3>
                    <h1 ref={h1}>Features</h1>
                </>}
                {lang && 
                <>
                    <h3 ref={h3} className='ru-h3'>   ?</h3>
                    <h1 ref={h1} className='ru-h1'></h1>
                </>}
                <div className="elements-info">
                    <div ref={list1} className="element">
                        <div onClick={() => setHidden1(prev => !prev)} className="open-info">
                            {hidden1 && <Circle />}
                            {!hidden1 && <CircleE />}
                            {!lang && <h2>SURVIVE AT ALL COSTS</h2>}
                            {lang && <h2 id='ru-h2'>  </h2>}
                        </div>
                            {!lang && hidden1 && <p className="hidden">
                                You have 30 minutes to find a relic, signal for extraction, and grab
                                one of three spots on the rescue chopper.
                            </p>}
                            {lang && hidden1 && <p className="hidden">
                                   30 ,   ,      
                                      .
                            </p>}
                    </div>
                    <div ref={list2} className="element">
                        <div onClick={() => setHidden2(prev => !prev)} className="open-info">
                            {hidden2 && <Circle />}
                            {!hidden2 && <CircleE />}
                            {!lang && <h2>Create allies and enemies</h2>}
                            {lang && <h2 id='ru-h2'>   </h2>}
                        </div>
                            {!lang && hidden2 && <p className="hidden">
                                Keep your eyes open for footprints 
                                and other indicators that help you stalk your enemies...
                            </p>}
                            {lang && hidden2 && <p className="hidden">
                                      ,
                                     ...
                            </p>}
                    </div>
                    <div ref={list3} className="element">
                        <div onClick={() => setHidden3(prev => !prev)} className="open-info">
                            {hidden3 && <Circle />}
                            {!hidden3 && <CircleE />}
                            {!lang && <h2>impress the audience</h2>}
                            {lang && <h2 id='ru-h2'> </h2>}
                        </div>

                            {!lang && hidden3 && <p className="hidden">
                                Along with your wits, you will have to equip yourself with various weapons found on 
                                the island. Guns are certainly an option, but melee weapons are just as deadly.
                            </p>}
                            {lang && hidden3 && <p className="hidden">
                                   ,     ,  
                                . , , ,       .
                            </p>}
                    </div>
                </div>
            </div>
            
        </article>
    )
}