import './styles/section3.css'
import { useContext, useLayoutEffect, useRef } from "react";
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {gsap, Power3} from 'gsap'
import { LangContext } from '../../../context/LangContext';
gsap.registerPlugin(ScrollTrigger)

export default function Section3() {
    const h1 = useRef(null)
    const h3 = useRef(null)
    const table = useRef(null)
    const section3 = useRef(null)

    useLayoutEffect(() => {
        gsap.from([h3.current, h1.current, table.current], {
            scrollTrigger: {
                start: 'center bottom',
                trigger: '.section3',
                
            },
            opacity: 0,
            y: -20,
            stagger: 0.5,
            duration: 0.5,
            ease: Power3.easeOut()
        })
        gsap.to(section3.current, {
            scrollTrigger: {
                scrub: 1,
                trigger: ".section3",
                start: 'bottom bottom',
            },
            opacity: 0,
            scale: 0.9
        })
    }, [])
    const {lang} = useContext(LangContext)
    return(
        <article ref={section3} className='section3' id='section3'>
            <div className="gradient"></div>
            <div className="content" id='content'>
                {!lang && <h3 ref={h3}>Can my computer run this game?</h3>}
                {lang && <h3 ref={h3} className='ru-h3'>     ?</h3>}
                {!lang && <h1 ref={h1}>System Requirements</h1>}
                {lang && <h1 ref={h1} className='ru-h1'> </h1>}

                <div className="table">
                    <table ref={table} align='top'>
                        <tr>
                            <td>
                                {!lang && <h2>OS:</h2>}
                                {lang && <h2 id='ru-h2'>:</h2>}
                                {!lang && <p>Windows 7 64-bit only(No OSX support at this time)</p>}
                                {lang && <p>Windows 7 64-bit only(    OSX )</p>}
                            </td>
                            <td>
                                {!lang && <h2>PROCESSOR:</h2>}
                                {lang && <h2 id='ru-h2'>:</h2>}
                                <p>Intel Core 2 Duo @ 2.4 GHZ or AMD Athlon X2 @ 2.8 GHZ</p>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                {!lang && <h2>MEMORY:</h2>}
                                {lang && <h2 id='ru-h2'> :</h2>}
                                <p>8 GB RAM</p>
                            </td>
                            <td>
                                {!lang && <h2>storage:</h2>}
                                {lang && <h2 id='ru-h2'>:</h2>}
                                {!lang && <p>8 GB available space</p>}
                                {lang && <p>8 GB  </p>}
                            </td>
                        </tr>
                        <tr>
                            <td colspan="2">
                                {!lang && <h2>Graphics:</h2>}
                                {lang && <h2 id='ru-h2'>:</h2>}
                                <p>NVIDIA GeForce GTX 660 2GB or AMD Radeon HD 7850 2GB DirectX11 (Shader Model 5)</p>
                            </td>
                            
                        </tr>
                    </table>
                </div>
            </div>
            
        </article>
    )
}