import './styles/footer.css'
import { ReactComponent as Socials } from './images/socials.svg';
import { useContext } from 'react';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
import { gsap } from 'gsap';
import { LangContext } from '../../../context/LangContext';

export default function Footer() {
    const {lang} = useContext(LangContext)
    function Scroll1() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section1'})
    }
    function Scroll2() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section2'})
    }
    function Scroll3() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section3'})
    }
    function Scroll4() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section4'})
    }
    return(
        <footer className='footer'>
            <div className="lvl1">
                <div className="logo">
                    <img src={require('./images/logo.png')} alt="logo" />
                </div>
                <nav className='footer-nav'>
                <ul> 
                    {/*      */}
                    {!lang && <>
                        <li><a onClick={Scroll1}>main</a></li>
                        <li><a onClick={Scroll2}>about</a></li>
                        <li><a onClick={Scroll3}>system requirements</a></li>
                        <li><a onClick={Scroll4}>Quotes</a></li>
                    </>}
                    {lang && <>
                        <li><a onClick={Scroll1}></a></li>
                        <li><a onClick={Scroll2}> </a></li>
                        <li><a onClick={Scroll3}> </a></li>
                        <li><a onClick={Scroll4}></a></li>
                    </>}
                </ul>
            </nav>
            <Socials />
        </div>
        <div className="line"></div>
        <div className="lvl2">
            <p className='copyright'>  2018 Anton Tsukanov, Inc. All Rights Reserved</p>
            <p className='menu'>
                <a href='#'>Privacy Policy</a>
                |
                <a href='#'>Terms of Services</a>
                |
                <a href='#'>Code of Conduct</a>
            </p>
        </div>
        </footer>
    )
}