import './styles/section5.css'
import { useContext, useLayoutEffect, useRef } from "react";
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {gsap, Power3} from 'gsap'
import { LangContext } from '../../../context/LangContext';
gsap.registerPlugin(ScrollTrigger)

export default function Section5() {
    const section5 = useRef(null)
    const h1 = useRef(null)
    const h3 = useRef(null)
    const descript = useRef(null)
    const sub = useRef(null)
    const input = useRef(null)

    const {lang} = useContext(LangContext)

    useLayoutEffect(() => {
        gsap.from(section5.current, {
            scrollTrigger: {
                trigger: '#section5',
                start: 'center bottom'
            },
            opacity: 0,
            duration: 0.5,
            scale: 0.5
        })
        gsap.from([h3.current, h1.current], {
            scrollTrigger: {
                trigger: '#section5',
                start: 'center bottom'
            },
            delay: 0.5,
            stagger: 0.5,
            duration: 0.5,
            opacity: 0,
            y: -20,
            ease: Power3.easeOut()
        })
        gsap.from(descript.current, {
            scrollTrigger: {
                trigger: '#section5',
                start: 'center bottom'
            },
            delay: 1.5,
            opacity: 0,
            duration: 0.5,
            x: -100,
            ease: Power3.easeOut()
        })
        gsap.from(input.current, {
            scrollTrigger: {
                trigger: '#section5',
                start: 'center bottom'
            },
            delay: 2,
            duration: 0.5,
            opacity: 0,
            x: -50,
        })
        gsap.from(sub.current, {
            scrollTrigger: {
                trigger: '#section5',
                start: 'center bottom'
            },
            delay: 2.5,
            duration: 0.5,
            opacity: 0,
            x: 50,
        })
    }, [])
    return(
        <article ref={section5} className='section5' id='section5'>
                <div className="content" id='content'>
                    <div className="right">
                        {!lang && <h3 ref={h3}>Want to stay in touch?</h3>} 
                        {!lang && <h1 ref={h1}>newsletter SUBSCRIBE </h1>}
                        {lang && <h3 ref={h3} className='ru-h3'>   ?</h3>} 
                        {lang && <h1 ref={h1} className='ru-h1'>    </h1>}

                       {!lang &&  <p ref={descript} className="descript">
                            In order to start receiving our news, all you have to do is enter your email address. 
                            Everything else will be taken care of by us. 
                            We will send you emails containing information about game. We dont spam.
                        </p>}
                        {lang &&  <p ref={descript} className="descript">
                                , ,    ,      .
                                 .
                                ,    .    .
                        </p>}
                       {!lang && <div className="bottom">
                            <div ref={input} className="input-email">
                                <p>Your email address</p>
                                <input placeholder='enter your email...' autoComplete={false} type="text" />
                            </div>
                            <button ref={sub} className="buy"><p>Subscribe now</p></button>
                        </div>}
                        {lang && <div className="bottom">
                            <div ref={input} className="input-email">
                                <p>  </p>
                                <input placeholder='  . ' autoComplete={false} type="text" />
                            </div>
                            <button ref={sub} className="buy"><p> </p></button>
                        </div>}
                    </div>
                </div>  
        </article>
    )
}