import { ReactComponent as SteamSVG } from './images/steam.svg';
import { ReactComponent as XboxSVG } from './images/xbox.svg';
import './styles/header.css'
import { useContext, useLayoutEffect, useState, useRef } from "react";
import {gsap, Power3} from 'gsap'
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';

import { LangContext } from '../../../context/LangContext';
gsap.registerPlugin(ScrollToPlugin)
export default function Header() {
    const [menu, setMenu] = useState(false);
    const {lang, toRu, toEn} = useContext(LangContext);
    const logo = useRef(null)
    const li1 = useRef(null)
    const li2 = useRef(null)
    const li3 = useRef(null)
    const li4 = useRef(null)
    const select = useRef(null)
    useLayoutEffect(() => {
        gsap.from([logo.current, li1.current, li2.current, li3.current, li4.current, select.current], {
            duration: 0.5,
            y: -70,
            stagger: 0.2,
            ease: Power3.easeOut(),
        })
    }, [])

    function selectLang() {
        if (document.getElementById('ru').selected) {
            toRu()
        }
        if (document.getElementById('en').selected) {
            toEn()
        }
    };
    function Scroll1() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section1'})
    }
    function Scroll2() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section2'})
    }
    function Scroll3() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section3'})
    }
    function Scroll4() {
        gsap.to(window, {duration: 0.5, scrollTo: '#section4'})
    }
    return(
        <header>
        <div ref={logo} className="logo">
            <img src={require('./images/logo.png')} alt="logo" />
        </div>
        <nav className='main-nav'>
            <ul> 
                {/*      */}
                {!lang && <>
                    <li ref={li1}><a onClick={Scroll1}>main</a></li>
                    <li ref={li2}><a onClick={Scroll2}>about</a></li>
                    <li ref={li3}><a onClick={Scroll3}>system requirements</a></li>
                    <li ref={li4}><a onClick={Scroll4}>Quotes</a></li>
                </>}
                {lang && <>
                    <li ref={li1}><a onClick={Scroll1}></a></li>
                    <li ref={li2}><a onClick={Scroll2}> </a></li>
                    <li ref={li3}><a onClick={Scroll3}> </a></li>
                    <li ref={li4}><a onClick={Scroll4}></a></li>
                </>}
            </ul>
        </nav>

        <div className="right">
            <div ref={select} className="vote">
                <select onChange={selectLang} name="lang" id="lang">
                    <option id='en' selected value="en">ENG</option>
                    <option id='ru' value="ru">RUS</option>
                </select>
            </div>
            <div className="line"></div>
            <div className="socials">
                <a href="#"><XboxSVG /></a>
                <a href='#'><SteamSVG /></a>
                <div id='open-menu' className="open-menu" onClick={() => setMenu(prev => !prev)}>
                    <div className="line line1"></div>
                    <div className="line line2"></div>
                    <div className="line line3"></div>
                </div>
            </div>
        </div>

        {menu && 
        <div className="menu-hidden" id='menu-hidden'>
            <div id='open-menu' className="open-menu" onClick={() => setMenu(false)}>
                    <div className="line line1"></div>
                    <div className="line line2"></div>
                    <div className="line line3"></div>
            </div>
        <nav id='main-nav'>
            <ul> 
                {!lang && <>
                    <li><a href="#">main</a></li>
                    <li><a href="#">about</a></li>
                    <li><a href="#">system requirements</a></li>
                    <li><a href="#">Quotes</a></li>
                </>}
                {lang && <>
                    <li><a href="#"></a></li>
                    <li><a href="#"> </a></li>
                    <li><a href="#"> </a></li>
                    <li><a href="#"></a></li>
                </>}
            </ul>
        </nav>
            <div id="socials">
                <a href="#"><XboxSVG /></a>
                <a href='#'><SteamSVG /></a>
            </div>
        </div>}
    </header>
    )
}